
import React from "react";
import { useSelector } from "react-redux";
import classes from './animeView.module.css'
import ReactPlayer from 'react-player/lazy'
import AnimeSearch from './../animeSearch/animeSearch'
const AnimeView = () =>{
    
    const description = useSelector(state => state.anime.currentAnime.description)
    const title_en = useSelector(state => state.anime.currentAnime.title_en)
    const title_jp = useSelector(state => state.anime.currentAnime.title_jp)
    
    const endDate = useSelector(state => state.anime.currentAnime.endDate)
    const popularityRank = useSelector(state => state.anime.currentAnime.popularityRank)
    const ageRating = useSelector(state => state.anime.currentAnime.ageRating)
    const showType = useSelector(state => state.anime.currentAnime.showType)
    const status = useSelector(state => state.anime.currentAnime.status)
    const posterImage = useSelector(state => state.anime.currentAnime.posterImage)
    const episodeCount = useSelector(state => state.anime.currentAnime.episodeCount)
    const totalLength = useSelector(state => state.anime.currentAnime.totalLength)
    const coverImage = useSelector(state => state.anime.currentAnime.coverImage)
    const youtubeVideoId = useSelector(state => state.anime.currentAnime.youtubeVideoId)
    const title = useSelector(state => state.anime.currentAnime.canonicalTitle)
    
    return(
        <div>
            <AnimeSearch />
            
            <div className={classes.cover}>
                <img src={coverImage} />
            </div>
            <div className={classes.items}>

                <div className={`${classes.item} ${classes.poster}`}>
                    <img src={posterImage} />
                </div>

                <div className={`${classes.item} ${classes.attrs}`}>
                    <h1 className={classes.title}> <span>{title} | {title_en || ''} | {title_jp || ''}</span></h1>
                    <h1 className={classes.description}><span>{description}</span></h1>
                    <h1 className={classes.dates}>Released: <span>{endDate || 'ongoing'}</span></h1>
                    <h1 className={classes.rank}>Rank: <span>{popularityRank}</span></h1>
                    <h1 className={classes.rating}>Age rating: <span>{ageRating}</span></h1>
                    <h1 className={classes.type}>Show type: <span>{showType}</span></h1>
                    <h1 className={classes.episodes}>Episodes: <span>{episodeCount || 'ongoing'}</span></h1>
                    <h1 className={classes.status}>Status: <span>{status}</span></h1>
                    <h1 className={classes.length}>Length: <span>{(totalLength / 60).toFixed(1)} hours</span></h1>
                    
                    <ReactPlayer url={`https://www.youtube.com/watch?v=${youtubeVideoId}`} />
                    
                    




                </div>
            </div>
        </div>
    )
}

export default AnimeView