import classes from './home.module.css';
import React from 'react'
import { useSelector } from 'react-redux';

const Welcome = function(){
    let theme = useSelector(state => state.theme.theme)

    return(
        <div className={(theme === 'light') ? classes.wrapper : classes.wrapperBlack}>
            <h1 className={classes.title}>Home</h1>
            <p className={classes.text}>Welcome to my <span className={classes.textMark}>SPA</span> use <span className={classes.textMark}>links</span> above to navigate through</p>
        </div>
    )
}

export default Welcome;