
import classes from './animeSearch.module.css'
import React, { useState } from 'react'
import { actions } from '../../../store/store'
import { useDispatch } from 'react-redux/es/exports'
import { useNavigate } from 'react-router'
import Description from '../../description/description'

const animeSearch = function(){

    const [anime,setAnime] = useState('')
    let dispatch = useDispatch()
    let navigate = useNavigate()
    let apicall = async function(animeName) {
        dispatch({type:actions.setFetch})
        console.log(`https://kitsu.io/api/edge/anime?filter[text]=${animeName}`)
        await fetch(`https://kitsu.io/api/edge/anime?filter[text]=${animeName}`)

    .then(res => res.json()).then(result => dispatch({type:actions.searchAnime,items: result.data}))}

    const onclickHandler = () =>{
        dispatch({type:actions.resetAnime})
        
        apicall(anime)
        
        navigate('/anime/list')
        
    }
    return(
        <>
            <Description description='Anime search tool using kitsu api'/>
            <div className={classes.wrapper}>
                <input className={ classes.input } type='text' value={ anime } onChange={ (e)=>setAnime(e.target.value) }/>
                <button className={ classes.button } onClick={ onclickHandler }></button>
            </div>
            
        </>
    )
}

export default animeSearch;