import { actions } from "./store"

const defaultStore = {
    notes : []
}

export let notesReducer = (state = defaultStore, action) =>{
        switch (action.type){
            
            case action.setNotes:
                return{...state, notes: action.payload}
            case actions.deleteNote:
                return{...state, notes: state.notes.filter(customer => customer.id !== action.payload)}
            case actions.addNote:
                return {...state, notes: [...state.notes, action.payload]}
        
            default:
                return state
        
}
}
        