import React, { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { actions } from "../../store/store";
import classes from './notesList.module.css'
import { v4 as uuidv4 } from 'uuid';
import Description from '../description/description'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';

import 'bootstrap/dist/css/bootstrap.min.css';
import Note from "./note/note";


export default function NotesList () {
    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')
    const [show, setShow] = useState(false);

    let dispatch = useDispatch()
    let notes= useSelector(state => state.notes.notes) 


    const handleClose = () => {
        let id = uuidv4()
        dispatch({type:actions.addNote, payload: {id: id, title: title,body:body}})
        setShow(false)};

    const handleShow = () => setShow(true);

    const bodyhandler = (e) => {
        setBody(e.target.value)
    }

    const titlehandler = (e) => {
        setTitle(e.target.value)
    }


    return(
        <div>
            <Description description={'A simple note app'}/>
            <>
                <Button variant="secondary" onClick={handleShow} style={{'display':'block', 'margin':'0 auto'}}>
                    Add note
                </Button>

                <Modal show={show} onHide={() => setShow(false)}>
                    <Modal.Header closeButton>
                    <Modal.Title>Create note</Modal.Title>
                    </Modal.Header>
                    
                        <Modal.Body>
                            <Form>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    
                                    <FloatingLabel controlId="floatingInputGrid" label="Note title">
                                    <Form.Control type="text" placeholder="" onChange={ titlehandler } maxLength={20}/>
                                    </FloatingLabel>
                                    
                
                                </Form.Group>

                                <FloatingLabel controlId="floatingTextarea2" label="Note body">
                                    <Form.Control
                                    as="textarea"
                                    maxLength={150}
                                    style={{'height':'100px'}}
                                    onChange={ bodyhandler }
                                    />
                                </FloatingLabel>
                                
                            </Form>
                        </Modal.Body>

                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShow(false)}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleClose}>
                            Add note
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>

            <div className={classes.list}>
                {notes.map(note => 
                (
                    <Note title={note.title} body={note.body} id={note.id}/>
                ))}
            </div>


        </div>

    )
}