

import { actions } from "./store"


const defaultStore = {
    theme: 'light',
}

export let themeReducer = (state = defaultStore, action) =>{

switch (action.type){

    
    case actions.switchWhite:
        return{...state, theme: 'light'}

    case actions.switchBlack:
        return{...state, theme: 'black'}

    default:
        return state

}
}





