

import { actions } from "./store"


const defaultStore = {
    count : 0,
}

export let counterReducer = (state = defaultStore, action) =>{

switch (action.type){

    case actions.increment:
        return {...state, count: state.count + 1}

    case actions.decrement:
        return {...state, count: state.count - 1}
    
    case actions.resetCounter:
        return{...state, count: 0}
    
    case actions.searchWeather:
        return {...state, weather: action.weather, city: action.city, country: action.country}
    
    case actions.weatherError:
        return{...state, weather: [], isWeatherError: actions.isError}

    default:
        return state

}
}





