
import { actions } from "./store"


const defaultStore = {
    weather : [],
    city : '',
    country: '',
    isWeatherError: false,
    isFetching: true,
    
}

export let weatherReducer = (state = defaultStore, action) =>{

switch (action.type){
    
    case actions.setFetch:
        return{...state, isFetching: true}
        
    case actions.searchWeather:
        return {...state, weather: action.weather, city: action.city, country: action.country, isFetching: false}
    
    case actions.weatherError:
        return{...state, weather: [], isWeatherError: actions.isError}

    default:
        return state

}
}
