
import React from "react"
import classes from './weatherView.module.css'
import { useSelector } from "react-redux";
import WeatherItem from "./weatherItem/weatherItem";
import Preloader from "../../decorative/switch/preloader/preloader";

export default function WeatherView (){

    let weather = useSelector(state => state.weather.weather)
    let city = useSelector(state => state.weather.city)
    let country = useSelector(state => state.weather.country)
    let isFetching = useSelector(state => state.weather.isFetching)
    

        return(
            <div>
                
                <h1 className={classes.city}>{`${city} ${country}`}</h1>
                {(weather.length !== 0 && isFetching === false)
                ?
                    <div className={classes.forecast}>
                        {weather.map(item =>{
                            return(
                                <WeatherItem date={item.valid_date} 
                                            icon={item.weather.icon} 
                                            temp={item.temp} 
                                            min_temp={item.min_temp}
                                            max_temp={item.max_temp}
                                            description={item.weather.description}
                                            wind_spd={item.wind_spd}
                                            wind_cdir_full={item.wind_cdir_full} />
                            
                            )
                        })}
        
                    </div>

                    : (isFetching === true)
                    ? <Preloader />
                    : <div className={classes.error}>City not found</div>
                    
                }
            </div>
        )
    }
    
    
