
import React from "react"
import classes from './animeItem.module.css'
import { useNavigate } from "react-router"
import { useDispatch } from "react-redux"
import { actions } from "../../../../store/store"

export default function AnimeItem(props){
    let apicall = async function(id) {
        await fetch(`https://kitsu.io/api/edge/anime/${id}`)
    .then(res => res.json()).then(result => dispatch({type:actions.setCurrAnime, 
        canonicalTitle: result.data.attributes.canonicalTitle,
        description: result.data.attributes.description,
        title_en: result.data.attributes.titles.en_jp,
        title_jp: result.data.attributes.titles.ja_jp,
        startDate: result.data.attributes.startDate,
        endDate: result.data.attributes.endDate,
        popularityRank: result.data.attributes.popularityRank,
        ageRating: result.data.attributes.ageRating,
        showType: result.data.attributes.showType,
        status: result.data.attributes.status,
        posterImage: result.data.attributes.posterImage.large,
        episodeCount: result.data.attributes.episodeCount,
        totalLength:  result.data.attributes.totalLength,
        coverImage: result.data.attributes.coverImage.small,
        youtubeVideoId: result.data.attributes.youtubeVideoId
    
    }))}

    let navigate = useNavigate()
    let dispatch = useDispatch()

    let onclickHandler = (e) =>{
        apicall(e.target.value)
        navigate('/anime/view')
    }
    
    return(
        <div className={classes.item}>
            <h1 className={classes.title}>{props.title}</h1>
            <img src={props.poster} className={classes.poster}/>
            <button className={classes.button} onClick={ onclickHandler } value={props.id}>More</button>
            <h2 className={classes.startDate}>Started:      <span className={classes.attrs}>{props.startDate}</span></h2>
            <h2 className={classes.episodeCount}>Episodes:  <span className={classes.attrs}>{props.episodeCount || 'ongoing'}</span></h2>
            <h2 className={classes.showType}>Type:          <span className={classes.attrs}>{props.showType}</span></h2>
            <h2 className={classes.popularity}>Rank:        <span className={classes.attrs}>{props.popularityRank}</span></h2>
            <h2 className={classes.rating}>Age rating:      <span className={classes.attrs}>{props.ageRating}</span></h2>
            <h2 className={classes.status}>Status:          <span className={classes.attrs}>{props.status}</span></h2>       
        </div>
    )
}



